                  <!-- <div class="showWhite  handlePictureOverflow keepHeightPictrue" :class="{positionBoxItem1:forIe9.change3,forIe10positionBoxItem1:forIe10.change1}"><img :src="imgLiyingUrl" class="imgWidth"/></div> -->
