<script>
import { mapState } from 'vuex';
import MaskList from '../../components/mask';
import NavBar from "../../components/NavBar";
import footBar from "../../components/foot";
import { handleClickEvent } from '../../util/util.js';
// import { truncate } from 'fs';
function IsMobile() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone",
                "SymbianOS", "Windows Phone",
                "iPad", "iPod"];
    var flag = false;
    for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = true;
            break;
        }
    }
    return flag;
   }

export default {
  components: {
    NavBar,
    MaskList,
    footBar
  },
  data(){
    return{
      img1Url:require('../../assets/icon/home_btn_normal.png'),
      imgTpgUrl:require('../../assets/img/salutation_image_trtc.png'),
      imgTseUrl:require('../../assets/img/salutation_image_ilvb.png'),
      imgLiyingUrl:require('../../assets/img/salutation_image_gme.png'),
      imgPingtaiUrl:require('../../assets/img/project_image_pingtai.png'),
      forIe9:{
          change1:false,
          change2:false,
          change3:false,
          change4:false,
      },
      forIe10:{
        change1:false,
      },
      changeHeight:false,
      textAlign:false,
      textSetWidth: false,
      middleScreenEn:false,
      marginTopIE:false,
    }
  },
   computed: {
    ...mapState({
      isMask: state => state.isMask,
      isAndroid: state => state.isAndroid,
      isiOS: state => state.isiOS,
    }),
      i18n(){
          return this.$i18n.locale
      },
  },
  watch: {
    isMask: function(val) {
      const indexH = document.querySelector('.index');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        indexH.style.overflow = val? 'hidden' : 'visible';
      })
    },

     i18n(){
      if(IsMobile()){    /* 移动端 */
          if(this.$i18n.locale=='en'){
               this.changeHeight = true
               this.textAlign = true
           }else{
               this.changeHeight = false
                this.textAlign = true
            }
         }else{
            // Pc
             if(this.$i18n.locale=='en'){
               this. middleScreenEn = true
             }else{
                this. middleScreenEn = false
             }
              this.textSetWidth = true
         }
      }  
  },
  methods:{
    stopPropatation($event){
        // var get= $event.stopPropatation || $event.cancelBubble  
        if(IsMobile()){
          return
        }else{
            $event.stopPropagation();
        }
       
    },
    go(){
      this.$router.push()
    },
    linkFn(num){
      if(num==1){
       
        window.open( 'https://cloud.tencent.com/product/trtc')
      }
      if(num==2){
       
         window.open('https://cloud.tencent.com/solution/ilvb')
      }
      if(num==3){
         if( IsMobile()){
          // this.handleClickEvent('82095','click_solution_GME')
        }
         window.open ('  https://cloud.tencent.com/product/gme')
      }
    },
     handleClickEvent(id, eventName, params) {
      handleClickEvent(id, eventName, params)
    }
  },
  mounted(){
    if(IsMobile()){
           this.$refs.mobileTouch1.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch1.style.background = "rgba(50,130,255,0.1)"
           this.$refs.mobileTouch1.style.border = "1px solid rgba(50,130,255,1)"

    })
    this.$refs.mobileTouch2.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch2.style.background = "rgba(50,130,255,0.1)"
           this.$refs.mobileTouch2.style.border = "1px solid rgba(50,130,255,1)"

    })
    this.$refs.mobileTouch3.addEventListener("touchstart",()=>{
           this.$refs.mobileTouch3.style.background = "rgba(50,130,255,0.1)"
           this.$refs.mobileTouch3.style.border = "1px solid rgba(50,130,255,1)"

    })
           this.$refs.mobileTouch1.addEventListener("touchend",()=>{
           this.$refs.mobileTouch1.style.background = null
           this.$refs.mobileTouch1.style.border = null
            // window.open( 'https://cloud.tencent.com/product/trtc')
          
    })
      this.$refs.mobileTouch2.addEventListener("touchend",()=>{
           this.$refs.mobileTouch2.style.background = null
           this.$refs.mobileTouch2.style.border = null
            // window.open('https://cloud.tencent.com/solution/ilvb')
           
    })
     this.$refs.mobileTouch3.addEventListener("touchend",()=>{
           this.$refs.mobileTouch3.style.background = null
           this.$refs.mobileTouch3.style.border = null
            // window.open ('  https://cloud.tencent.com/product/gme')
    })
    }
   
    if(navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE","")) == 9){
        this.forIe9.change1 = true
        this.forIe9.change2 = true
        this.forIe9.change3 = true
        this.forIe9.change4 = true
         this.marginTopIE = true

    }
     if(navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE","")) == 10){
        this.forIe10.change1 = true
        this.forIe9.change3 = true
        this.forIe9.change4 = true
         this.marginTopIE = true
    }

     if(navigator.userAgent.indexOf(("rv:11.0")) >  -1){
        // ("您的浏览器版本IE11")
      this.marginTopIE = true
    }
    

    if(IsMobile()){    /* 移动端 */
          if(this.$i18n.locale=='en'){
               this.changeHeight = true
               this.textAlign = true
           }else{
               this.changeHeight = false
               this.textAlign = true
            }
         }else{
          //  pc
             if(this.$i18n.locale=='en'){
               this. middleScreenEn = true
                this.textSetWidth = true
             }else{
                this. middleScreenEn = true
                 this.textSetWidth = true
             }
         }
  }
}
</script>
